<template>
  <div>
    <el-container>
      <el-header>
        <AppHeader>手机登录</AppHeader>
      </el-header>
      <el-main>
      <div class="input-content">
            <p class="input-item">
                <i class="iconfont icon-phone"></i>
                <input type="tel" placeholder="请输入手机号" class="input-text" v-model="phoneNumber" >
            </p>
            <p class="input-item">
                <i class="iconfont icon-password"></i>
                <input type="password" placeholder="请输入密码" class="input-text" autocomplete="off" v-model="password">
            </p>
            <el-button round @click="login" :class="{active:isComplete}">登录</el-button>
           
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import AppHeader from './component/AppHeader.vue'
import {mapMutations} from "vuex";
export default {
  name:'register',
  components:{
    AppHeader
  },
  data() {
    return {
      password:'',
      phoneNumber:''
    }
  },
  methods:{
    ...mapMutations(['setToken']),
   async login(){
     if(this.testNumber&&this.password){
      //  验证手机号是否存在,-1不存在
       const {data:i}=await this.$http.get('/cellphone/existence/check',{params:{phone:this.phoneNumber}})
       console.log(i)
       if(i.exist!==-1){//验证成功
         this.setToken({token: i.token});    //store中的为token赋值方法
        //  console.log(this.$store.token)
      const {data:res}=await this.$http.get('/login/cellphone',{params:{
        phone:this.phoneNumber,
        password:this.password
      }})
      console.log(res)
      if(res.code===502){
          this.$message.error('密码错误')
          return this.password=''
      }
         this.$router.push('/find')
       }else{
         return this.$message.error('该号码未注册')
       }
 
     }
    }
  },
  computed:{
   
    isComplete(){
      return this.phoneNumber.length && this.password.length
    },
    // 手机号验证
    testNumber(){
      return /^1[3578]\d{9}$/.test(this.phoneNumber)
    },
    // 密码验证
    testPassword(){
      return this.password.length>5
    },
    
   
  }
}
</script>

<style lang='less' scoped>
.el-header {
  padding: 0;
  margin: 0;
}

.el-main{
  width: 100%;
 overflow: hidden;
  .input-item{
   width: 94%;
    height: 45px;
    margin-left: 3%;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
 
  .iconfont{
   font-size: 20px;
    margin-left: 5px;
    margin-right: 12px;
    color: #999;
  }
 .el-button{
   width: 80%;
   margin-left: 10%;
    background-color: #e25a4a;
   color: #dd7367;
   font-size: 18px;
   margin-top: 24px;
 }
 .active{
   background-color: #de464e;
   color: #fff;
 }
}

 
</style>