<template>
    <div>
        <el-header>
            <AppHeader>手机号验证</AppHeader>
        </el-header>
        <el-main>
            <div class="checkmsg">
                <p>你的手机号：+86 {{$route.query.phoneNumber|toPrivacy}}</p>
                <p>你会收到一条带验证码的短信，请输入验证码</p>
            </div>
            <div class="checkcode">
                <div class="code-line" v-for="(item,index) in captchalist" :key="index">{{item}}</div>
            </div>
            <input type="tel" ref="captchaInputArea" v-model="captcha" class="hide">
            <button class="resend">重新发送/倒计时</button>
        </el-main>
    </div>
</template>

<script>
import AppHeader from './component/AppHeader'
export default {
    name:'CheckCode',
    components:{
        AppHeader
    },
    // 通过过滤器实现号码隐私
    filters:{
        toPrivacy(value){
            return value.split('').map((value,index)=>{
                if(index>2&&index<7){
                    return '*'
                }else{
                    return value
                }
            }).join('')
        }
    },
    data(){
        return{
            captchalist:['','','',''],
            captcha:''
        }
    },
    // 自动获取焦点
    mounted(){
        this.$refs.captchaInputArea.focus()
    },
    // 监控输入框的变化
    watch:{
        captcha(newVal,oldVal){
           console.log(this.$route.query.password)
            // 拆分输入框字符串,同时判断新旧值的大小来实现删除
            
            if(newVal.length>oldVal.length){
                Object.assign(this.captchalist,[...newVal])
            }else if(newVal.length<oldVal.length){
               this.captchalist= Object.assign([...newVal],this.captchalist)
            }
            if(newVal.length===4){
              this.$http.post('/register/cellphone',{  
                    captcha:newVal,
                    phone:this.$route.query.phoneNumber,
                    password:this.$route.query.password,
                    nickname:this.$route.query.nickname,
                    }
                ).then(res=>{
                     console.log(res)
                }).catch(err=>{
                    console.log(err)
                })
               
            }
        }
    },
    methods:{
      
    },
    
}
</script>

<style lang='less' scoped>
.el-header{
    padding: 0;
    margin: 0;
    //  padding: 59px 17px;
    
}
.checkmsg{
    color: #969798;
    font-size: 14px;
}
.checkcode{
    display: flex;
    justify-content: space-around;
    .code-line{
        height: 58px;
        width: 58px;
        line-height: 58px;
        text-align: center;
        border-bottom: 2px solid #969798;
    }
}
// 隐藏输入框
.hide{
    width: 0;
    height: 0;
    border: none;
    outline: none;
}
button{
    border: none;
}
.resend{
    float: right;
    margin: 20px;
    background: none;
}
</style>