<template>
  <div>
    <el-container>
      <el-header>
        <AppHeader>手机注册</AppHeader>
      </el-header>
      <el-main>
      <div class="input-content">
     
            <p class="input-item">
                <i class="iconfont icon-phone"></i>
                <input type="text" v-model="phoneNumber"
                 placeholder="请输入手机号"
                  class="input-text"
                  autocomplete="off" autofocus>
                 <!-- <el-input  placeholder="请输入手机号"></el-input> -->
            </p>
            <p class="input-item">
                <i class="iconfont icon-password"></i>
                <input type="password"  v-model="password"
                 placeholder="请设置密码,不少于6位" 
                  
                  class="input-text" >
            </p>
             <p class="input-item">
                <i class="iconfont icon-haoyou"></i>
                <input type="text"  v-model="nickname"
                 placeholder="请设置昵称" 
                  
                  class="input-text" >
            </p>
            
            <el-button round :class="['next',{'active_next':isComplete}]" @click="toCheckCode">下一步</el-button>
           
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>

import AppHeader from './component/AppHeader.vue'

export default {
  components:{
    AppHeader
  },
  data() {
    return {
  password:'',
  phoneNumber:'',
  nickname:''
    }
  },
  methods:{
    
     async toCheckCode(){
        if(this.testNumber&& this.testPassword&&this.tesNickname){
        // const{data:res}=await this.$http.get(`/cellphone/existence/check?phone=${this.phoneNumber}`)
        const{data:res}=await this.$http.get('/cellphone/existence/check',{params:{phone:this.phoneNumber}})
        // exist=-1未被注册,2已注册
          // console.log(res)
          // 未注册
          if(res.exist===-1){
            // 跳转页面
             this.$router.push({path:'/teleregister/checkcode',query:{
               phoneNumber:this.phoneNumber,
               password:this.password,
               nickname:this.nickname}})
            //  code=200,验证码发送成功
           
            const res= await this.$http.post(`/captcha/sent?phone=${this.phoneNumber}`)
            // console.log(res) 
          }else{
            return this.$message.error('手机号码已经注册')
          } 
        }else{
          this.$message.error('请输入合法的手机号或者密码')
        }
      
    },
    
        

  },
  computed:{
  
  //  完成填写
    isComplete(){
      return this.phoneNumber.length && this.password.length && this.nickname.length
    },
    // 手机号验证
    testNumber(){
      return /^1[3578]\d{9}$/.test(this.phoneNumber)
    },
    // 密码验证
    testPassword(){
      return this.password.length>5
    },
    tesNickname(){
      return this.nickname.length>0
    }
  }
}
</script>

<style lang='less' scoped>
.el-header {
  padding: 0;
  margin: 0;

}
.el-main{
  width: 100%;
 overflow: hidden;
  .input-item{
   width: 94%;
    height: 45px;
    margin-left: 3%;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .el-main/deep/.el-input{
           border:1px solid red;
           outline: none;
         }
  }
  
  .iconfont{
   font-size: 20px;
    margin-left: 5px;
    margin-right: 12px;
    color: #999;
  }
 .el-button{
   width: 80%;
   margin-left: 10%;
   background-color: #de464e;
   color: #fff;
   font-size: 18px;
   margin-top: 24px;
  outline-style: none;
 }
 .next{
   background-color: #e25a4a;
   color: #dd7367;
 }
 .active_next{
   background-color: #de464e;
   color: #fff;
 }
}

 
</style>